<template>
    <div class="main-xs">
        <Address-Header title=""></Address-Header>
        <div class="address">
            <div class="address-box">
                <label for=""></label>
                <input type="text" v-model="name" placeholder="">
            </div>

             <div class="address-box">
                <label for=""></label>
                <input type="text" v-model="phone" placeholder="">
            </div>

             <div class="address-box">
                <label for=""></label>
                <input type="text" v-model="zone" placeholder="">
            </div>

             <div class="address-box">
                <label for=""></label>
                <input type="text" v-model="detail" placeholder="">
            </div>
        </div>

        <div class="footer">
            <a @click="btn"></a>
        </div>
    </div>
</template>

<script>
import AddressHeader from "../../../common/header";
import { mapGetters, mapMutations } from "vuex";
import { Toast } from "mint-ui";
import { setCookie, getCookie } from "../../../assets/js/cookie.js";
import { user_address,address } from "../../../assets/js/api.js";
import axios from "axios";
export default {
  name: "add_address",
  data() {
    return {
      name: "",
      phone: "",
      zone: "",
      detail: ""
    };
  },
  components: {
    AddressHeader
  },
  methods: {
    btn(list) {
      let cookier_token = getCookie("token");
      axios({
        url: address,
        method: "post",
        params: {
            token:cookier_token,
            name:this.name,
            phone: this.phone,
            address:this.zone,
            xx_address: this.detail
        }
      })
        .then(res => {
             Toast({
              message: res.data.msg,
              duration: 950
            });
            this.$router.push({
                path:'/main/address'
            })
        })
        .catch(err => {
          //
        });
    }
  }
};
</script>

<style lang="stylus" scoped>
.main-xs{
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 99999;
    background: #f4f4f4;
}
.address {
    padding-top: 1.45rem;

    .address-box {
        width: 100%;
        height: 1.5rem;
        line-height: 1.5rem;
        background: #fff;

        label {
            width: 30%;
            height: 100%;
            padding-left: 0.58rem;
            font-size: 0.4rem;
            display: block;
            float: left;
        }

        input {
            width: 70%;
            font-size: 0.4rem;
        }
    }
}

.footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 1.3rem;
    border-top: 1px solid #e0e0e0;
    background: #ffffff;

    a {
        float: none;
        display: block;
        margin: 0.18rem auto;
        text-align: center;
        width: 95%;
        height: 0.9rem;
        line-height: 0.9rem;
        border-radius: 20px;
        font-size: 0.28rem;
        color: #fff;
        background-color: #00acff;
    }
}
</style>



