<template>
  <div class="o1">
      <Order-Header title=""></Order-Header>
        <div class="details-box">
      <div class="details-success">
          <p></p>
          <img src="https://shopstatic.vivo.com.cn/vivoshop/wap/dist/images/membercenter/order/no-pay_79c2dfe.png">
      </div>
    
        <div class="details-address">
        <p class="address-box">
            <span class="name">Myfwk</span>
            <span class="phone">15255460858</span>
        </p>
        <p class="address-details">B </p>
      </div>
      <div class="details-list" v-for="(list,index) in o1" :key="index">
        <div class="details-list-1">
            <img :src="list.homeImg">
            <p>
                <!-- <span>id{{list.id}}</span> -->
                <span class="name">{{list.homeName}}<p> {{$route.query.value}}</p></span>
                <span class="price"> {{list.homePrice}}</span>
            </p>
        </div>
       
       <div class="details-list-2">
           <div class="details-list-2-1">
                <p>
                    <span class="span-1"></span>
                    <span class="span-2">68</span>
                </p>
                 <p>
                    <span class="span-1"></span>
                    <span class="span-2">{{$route.query.ly}}</span>
                </p>
           </div>

           <div class="details-list-2-2">
                <p>
                    <span class="span-1"></span>
                    <span class="span-2"> 2898.00</span>
                </p>
                 <p>
                     <span class="span-1"></span>
                     <span class="span-2"> 0.00</span>
                </p>
                <p>
                     <span class="span-1"></span>
                     <span class="span-2 red">- 0.00</span>
                </p>
                <p>
                    <span class="span-1"></span>
                    <span class="span-2 red">- 0.00</span>
                </p>
           </div>

            <div class="details-list-2-3">
                <p>
                    <span class="span-1"></span>
                    <span class="span-2"> </span>
                </p>
                 <p>
                     <span class="span-1"></span>
                     <span class="span-2">{{$route.query.listname}}</span>
                </p>
                <p>
                     <span class="span-1"></span>
                     <span class="span-2"></span>
                </p>
                <p>
                    <span class="span-1"></span>
                    <span class="span-2">{{$route.query.text}}</span>
                </p>
           </div>
       </div>
        <!-- <img :src="list.homeImg" /> -->
        
        <div class="order-footer">
            
            <span>{{list.homePrice}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import OrderHeader from "../common/header";
export default {
  name: "o1",
  data() {
    return {
      o1: []
    };
  },
  components: {
    OrderHeader
  },
  created() {
    var _this = this;
    var id = this.$route.query.id;
    axios.get("/static/ceshi.json").then(function(res) {
      var data = res.data.data.home;
      for (var i = 0; i < data.length; i++) {
        if (data[i].id == id) {
          _this.o1.push(data[i]);
        }
      }
    });
    axios.get("/static/ceshi.json").then(function(res) {
      var data = res.data.data.list;
      for (var i = 0; i < data.length; i++) {
        if (data[i].id == id) {
          _this.o1.push(data[i]);
        }
      }
    });
  }
};
</script>

<style lang="stylus" scoped>
.details-box {
    position: absolute;
    top: 1.45rem;
}

.details-success {
    width: 100%;
    height: 2.4rem;
    background: #69c1ff;

    p {
        color: #fff;
        font-size: 0.46rem;
        line-height: 2.4rem;
        float: left;
        padding-left: 0.4rem;
    }

    img {
        width: 2.7rem;
        margin: 0.5rem;
        float: right;
    }
}

.details-address {
    width: 100%;
    height: 2.7rem;
    background: #fff;

    .address-box {
        width: 87%;
        margin: auto;
        font-size: 0.4rem;
        padding-top: 0.3rem;
        padding-bottom: 0.3rem;

        .phone {
            float: right;
        }
    }

    .address-details {
        width: 87%;
        margin: auto;
        color: #666;
        font-size: 0.38rem;
    }
    
}

.details-list {
    width: 100%;
    margin-bottom: 1.3rem;

    .details-list-1 {
        width: 100%;
        height: 3rem;
        background: #ffffff;
        margin-top: 5px;

        img {
            width: 2.5rem;
            margin: 0.28rem;
            float: left;
        }

        p {
            display: flex;
            flex-direction: column;

            .name {
                font-size: 0.4rem;
                margin-top: 0.3rem;
                height: 0.6rem;
            }

            .price {
                color: red;
                font-size: 0.35rem;
                font-weight: 500;
                height: 0.6rem;
            }

            span {
                p {
                    float: right;
                    margin-right: 0.5rem;
                }
            }
        }
    }

    .details-list-2 {
        width: 100%;
        height: 9rem;
        background: #ffffff;

        div {
            width: 90%;
            margin: auto;
            padding-top: 0.3rem;
            padding-bottom: 0.3rem;
            font-size: 0.36rem;
            border-top: 1px solid #e8e8e8;

            span {
                color: #888;
                float: left;
            }

            .span-1 {
                width: 2.3rem;
                display: block;
            }
        }

        .details-list-2-1 {
            height: 2rem;

            p {
                height: 0.76rem;
                line-height: 0.76rem;
            }
        }

        .details-list-2-2 {
            height: 3.5rem;

            p {
                height: 0.76rem;
                line-height: 0.76rem;
            }

            .red {
                color: red;
            }
        }

        .details-list-2-3 {
            height: 4rem;

            p {
                height: 0.76rem;
                line-height: 0.76rem;
            }
        }
    }

    .order-footer {
        width: 100%;
        height: 1.3rem;
        background: #fff;
        position: fixed;
        bottom: 0;
        line-height: 1.3rem;
        padding-left: 0.5rem;
        font-size: 0.38rem;
        border-top: 1px solid #eaeaea;

        span {
            color: red;
        }
    }
}
</style>



