<template>
    <div>
        <Pay-Header title=""></Pay-Header>
        <div @click="go_Xzaddress" class="pay-address">
             <p class="address-box">
                <span class="name">{{address_name}}</span>
              <span class="phone">{{address_phone}}</span>
            </p>
            <p class="address-details">
                {{address}}{{xz_address}}
            </p>
        </div>
        <div class="pay-shop" v-for="(list,index) in pay" :key="index">
            <div class="pay-shop-list">
                <p class="pay-shop-1"></p>
                <p class="pay-shop-2">
                    <img :src="list.fm_img" />
                    <p class="pay-shop-2-box">
                        <span class="name">{{list.name}}<p> 1</p></span>
                        <!-- <span></span> -->
                        <span class="price">{{list.price}}</span>
                    </p>
                </p>
            </div>

            <div class="pay-shop-invoice">
                <p class="pay-invoice-1"></p>
                <div class="pay-invoice-2">
                    <div class="pay-invoice-2-2">
                        <div>
                            <p>*:</p>
                            <input type="text" id="input" v-model="invoice"  placeholder="">
                            
                        </div>
                    </div>
                   
                </div>
            </div>

            <div class="pay-shop-fs">
                <div class="pay-fs-1"></div>
                <div class="pay-fs-2">
                    <!-- <div class="pay-fs-2-1" v-for="(item,index) in lists" :class="{active:index===ceshi}" @click="btn(index)" >
                        {{item.name}}
                    </div> -->
                    <div class="pay-fs-2-1" >
                        <div v-for="(list,index) in lists" :class="{active:index===listIndex}" @click="btn(list.name,index)">{{list.name}}</div>
                    </div>
                    <div class="pay-fs-2-2">
                       <div v-show="listIndex===0" class="pay-fs-2-2-1"></div>
                       <div v-show="listIndex===1" class="pay-fs-2-2-2">0</div>
                       <div v-show="listIndex===2" class="pay-fs-2-2-3"></div>
                    </div>
                </div>
                
            </div>

            <div class="pay-shop-liuyan">
                <p class="pay-liuyan-1"></p>
                <div class="pay-liuyan-2">
                    <textarea rows="5" v-model="content" placeholder="300)" maxlength="300"></textarea>
                    <p>{{list.price}}</p>
                    <p>0.00</p>
                    <p>0.00</p>
                    <p>{{list.price * 0.05 | integral}}</p>
                </div>
            </div>
           
            <div class="pay-shop-footer">
                <p class="price"><span>{{list.price}}</span></p>
                <a class="order" @click="addOrder(list,index)"></a>
            </div>
        </div>
    </div>
</template>
<style lang="stylus" scoped>
