                }
            }

            .price {
                color: red;
                font-size: 0.35rem;
                font-weight: 500;
                height: 0.6rem;
            }
        }
    }

    .pay-shop-liuyan {
        width: 100%;
        height: 6.5rem;
        background: #fff;
        margin-top: 0.3rem;
        margin-bottom: 0.3rem;

        .pay-liuyan-1 {
            width: 100%;
            height: 1.5rem;
            line-height: 1.5rem;
            border-bottom: 1px solid #eaeaea;
            font-size: 0.4rem;
            padding-left: 0.7rem;
        }

        .pay-liuyan-2 {
            width: 90%;
            margin: auto;

            textarea {
                width: 100%;
                height: 2rem;
                border: 1px solid #d1d1d1;
                border-radius: 3px;
                padding: 0.15rem 0.2rem;
                margin: 0.3rem auto;
                display: block;
            }

            p {
                color: #888;
                height: 0.48rem;
                font-size: 0.34rem;
            }
        }
    }

    .pay-shop-fs {
        width: 100%;
        height: 5rem;
        background: #ffffff;

        .pay-fs-1 {
            width: 100%;
            height: 1.5rem;
            line-height: 1.5rem;
            border-bottom: 1px solid #eaeaea;
            font-size: 0.4rem;
            padding-left: 0.7rem;
        }

        .pay-fs-2 {
            width: 100%;
            height: 3.5rem;
            background: #ffffff;

            .pay-fs-2-1 {
                width: 100%;
                height: 40%;
                font-size: 0.35rem;
                // background yellow
                display: flex;
                justify-content: center;
                align-items: center;

                div {
                    display: block;
                    width: 2.88rem;
                    height: 0.9rem;
                    line-height: 0.9rem;
                    border: 1px solid #d1d1d1;
                    border-radius: 3px;
                    margin: 0.1rem;
                    float: left;
                    text-align: center;
                }
            }

            .pay-fs-2-2 {
                width: 100%;
                height: 60%;
                font-size: 0.35rem;

                // background red
                div {
                    width: 90%;
                    height: 1.3rem;
                    border-radius: 3px;
                    border: 1px solid #d1d1d1;
                    margin: auto;
                    padding: 0.3rem;
                }

                .pay-fs-2-2-2 {
                    height: 1.56rem;
                }
            }
        }
    }
}

.pay-shop-footer {
    width: 100%;
    height: 1.5rem;
    border-top: 1px solid #eaeaea;
    background: white;
    position: fixed;
    bottom: 0;

    .price {
        float: left;
        line-height: 1.5rem;
        font-size: 0.43rem;
        color: #666;
        padding-left: 0.3rem;

        span {
            color: red;
        }
    }

    .order {
        width: 3.3rem;
        height: 0.9rem;
        line-height: 0.9rem;
        font-size: 0.35rem;
        margin-top: 0.3rem;
        margin-right: 0.3rem;
        border-radius: 30px;
        text-align: center;
        color: #fff;
        background: #f81200;
        float: right;
    }
}
</style>


<script>
import { Toast } from "mint-ui";
import { mapGetters, mapMutations } from "vuex";
import PayHeader from "../../common/header";
import { setCookie, getCookie } from "../../assets/js/cookie.js";
import { home_data,add_order } from "../../assets/js/api.js";
import axios from "axios";
export default {
  name: "pay",
  data() {
    return {
      listIndex: 0,
      invoiceIndex: 0,
      integral:'', //
      pay: [],
      address:[],
      addressId:'5',
      address_name:'1', //
      address_phone:'15255460858', //
      address:'',
      xz_address:'',
      content:'',//
      invoice:'',//
      swiperImg:'',
      lists: [
        {
          id: "1",
          name: ""
        },
        {
          id: "2",
          name: ""
        },
        {
          id: "3",
          name: ""
        }
      ],
    };
  },
  components: {
    PayHeader
  },
  
     computed: {
          xzAddess() {
            return this.$store.state.xzAddess;
          },
          ...mapGetters(
              ["this.$store.state.xzAddess"],
          )
      },
      filters:{
          integral(value){
            return value.toFixed(2) //
          }
      },
  methods: {
    go_Xzaddress(){
        this.pay.forEach(list=>{
            this.$router.push({
                path: "xz_address",
                query:{
                    id:list.id
                }
            });
      })
    },
    btn(id, index) {
      this.listIndex = index;
    },
    invoiceClick(index) {
      this.invoiceIndex = index;
    },
    addOrder(list) {
        let cookier_token = getCookie("token");
         axios({
            url:add_order,
            method: "post",
            params: {
                token:cookier_token,
                id:list.id,
                shop_name:list.name,
                shop_price:list.price,
                address_name:this.address_name,
                address_phone:this.address_phone,
                content:this.content,
                invoice:this.invoice,
                address:this.address+this.xz_address,
                shop_img:list.fm_img,
                integral:(list.price*0.05).toFixed(2)
            }
        })
            .then(res => {
                console.log(res)
                Toast({
                message: res.data.msg,
                duration: 950
                });
            })
            .catch(err => {
            });
      }
     
    
  },
  created() {
    var _this = this;
    var id = this.$route.query.id;

    axios.get(home_data).then(function(res) {
      for (var i = 0; i < res.data.data.length; i++) {
        if (res.data.data[i].id == id) {
          _this.pay.push(res.data.data[i]);
        }
      }
    });

    var addressId=this.$route.query.addressId
      this.$store.state.xzAddess.forEach(list=>{
        if(list.id==addressId){
            this.address_name=list.name
            this.address=list.address
            this.xz_address=list.xx_address
            this.address_phone=list.phone
            this.swiperImg=list.fm_img
        }
        
      })

   
  }
 
};
</script>





